import { useContentMetaApiData } from '@content/hooks/useContentMetaApiData';
import { useEffect, useState } from 'react';
import { getRegionKey } from '@content/i18n/utils';

const YT_PAGE_CHANGED_EVENT = 'yt-page-type-changed';
const WATCH_PATH = '/watch';

export function useIframeUrl() {
  const [iframeUrl, setIframeUrl] = useState<string | null>(null);
  const [isWatchPage, setIsWatchPage] = useState(
    window.location.pathname === WATCH_PATH,
  );
  const metaApiData = useContentMetaApiData();

  useEffect(() => {
    const handleLocationChange = () => {
      const isWatch = window.location.pathname === WATCH_PATH;
      setIsWatchPage(isWatch);
    };

    window.addEventListener(YT_PAGE_CHANGED_EVENT, handleLocationChange);
    return () => {
      window.removeEventListener(YT_PAGE_CHANGED_EVENT, handleLocationChange);
    };
  }, []);

  useEffect(() => {
    const baseBannerUrls = metaApiData?.adfoxBannerUrl_v2;
    const watchBannerUrls = metaApiData?.adfoxBannerUrlSecondary_v2;
    if (!baseBannerUrls && !watchBannerUrls) return;

    const setUrl = async () => {
      const urls = isWatchPage ? watchBannerUrls : baseBannerUrls;
      const key = await getRegionKey();
      const url = urls?.[key];
      setIframeUrl(url ?? null);
    };

    void setUrl();
  }, [
    isWatchPage,
    metaApiData?.adfoxBannerUrl_v2,
    metaApiData?.adfoxBannerUrlSecondary_v2,
  ]);

  return iframeUrl;
}
