import { useBanner } from '@content/components/YouboostAds/hooks/useBanner';
import { useIframeUrl } from '@content/components/YouboostAds/hooks/useIframeUrl';
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';

enum AdsBannerMessageTypes {
  AD_BANNER_SIZE_CHANGED = 'AD_BANNER_SIZE_CHANGED',
  AD_BANNER_CLOSED = 'AD_BANNER_CLOSED',
}

export interface AdsBannerSizeChangedMessage {
  type: AdsBannerMessageTypes.AD_BANNER_SIZE_CHANGED;
  width: number;
  height: number;
}

export interface AdsBannerClosedMessage {
  type: AdsBannerMessageTypes.AD_BANNER_CLOSED;
}

const isAdsBannerSizeChangedMessage = (
  data: unknown,
): data is AdsBannerSizeChangedMessage => {
  return (
    !!data &&
    typeof data === 'object' &&
    'type' in data &&
    data.type === AdsBannerMessageTypes.AD_BANNER_SIZE_CHANGED &&
    'height' in data &&
    typeof data.height === 'number' &&
    'width' in data &&
    typeof data.width === 'number'
  );
};

const isAdsBannerClosedMessage = (
  data: unknown,
): data is AdsBannerClosedMessage => {
  return (
    !!data &&
    typeof data === 'object' &&
    'type' in data &&
    data.type === AdsBannerMessageTypes.AD_BANNER_CLOSED
  );
};

export function AdfoxBanner() {
  const iframeUrl = useIframeUrl();
  const iframeRef = useRef<HTMLIFrameElement>(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const { isShown, handleCloseClick } = useBanner();

  const handleIframeLoad = useCallback(() => {
    setIsLoaded(true);
  }, []);

  const style = useMemo(
    () => ({
      opacity: isLoaded ? 1 : 0,
      height: isLoaded ? 'auto' : 0,
      transition: 'opacity 0.3s ease-in-out, height 0.3s ease-in-out',
    }),
    [isLoaded],
  );

  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      if (iframeUrl && event.origin !== new URL(iframeUrl).origin) return;
      if (!iframeRef.current) return;

      if (isAdsBannerSizeChangedMessage(event.data)) {
        const data = event.data;

        const height = data.height > 19 ? data.height + 19 : 0;
        const width = data.width > 19 ? data.width + 19 : 0;

        const adfoxContainer = document.querySelector('.adfox-container');

        if (adfoxContainer && width > 0) {
          (adfoxContainer as HTMLElement).style.width = `${width}px`;
        }

        iframeRef.current.style.height = `${height}px`;
        return;
      }

      if (isAdsBannerClosedMessage(event.data)) {
        void handleCloseClick();
        return;
      }
    };

    window.addEventListener('message', handleMessage);

    return () => {
      window.removeEventListener('message', handleMessage);
    };
  }, [iframeUrl, handleCloseClick]);

  if (!isShown || !iframeUrl) {
    return null;
  }

  return (
    <div className="adfox-container" style={style}>
      <iframe ref={iframeRef} src={iframeUrl} onLoad={handleIframeLoad} />
    </div>
  );
}
